body {
  font-family: "Inter", sans-serif;
  background-color: #000;
  color: #fff;
}

* {
  box-sizing: border-box;
}

section {
  p {
    text-align: center;
  }

  &:nth-child(2n) {
    p {
      width: 100%;
    }
  }
  &:nth-of-type(3n) {
    height: 100vh;
  }
}

button {
  background: #ffc145;
  border-radius: 60px;
  border: none;
  padding: 1rem 1.5rem;
  width: 20%;
}

svg {
  position: absolute;
  z-index: -20;
}

.terms__conditions {
    font-weight: bold;
    color: #db2763;
    font-size: 20px;
    
  }
  

@media screen and (max-width: 992px) {
  button {
    background: #ffc145;
    border-radius: 60px;
    border: none;
    padding: 1rem 1.5rem;
    width: 25%;
  }


  .terms,
  .terms__conditions {
    font-size: 20px;
  }
}

@media screen and (max-width: 442px) {
  .terms,
  .terms__conditions {
    font-size: 15px;
  }
  button {
    width: 70%;
  }
}
